body {
  background-color: #c9af98;
}

#homeIcon {
  height: 40px;
  width: 40px;
}

#homeNavBar {
  color: hsl(0, 0%, 11%);
}

.wordIcons {
  display: none;
}

@media (max-width: 990px) {
  #homeNavBar {
    background-color: #c9af98;
  }
  .icons {
    display: none;
  }
  .wordIcons {
    display: block;
  }
}

/* #cal {
  height: 30px;
  width: 30px;
} */

#homeCon {
  min-height: 400px;
  min-width: 600px;
  padding-bottom: 5%;
  color: #e1e8f0;
  font-family: "Ubuntu", sans-serif;

  /* height: 800px; */
}

#homeWelcome {
  /* position: absolute;
  top: 50%; */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  margin-top: 30%;
  text-shadow: 2px 2px black;
}

#masterHistory {
  width: 90%;
  margin: 1rem auto;
  max-width: 1000px;
}

#classicShelbyCon {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
  width: 100%;
}

#classicShelbyCon .image {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  border: black thin solid;
}

#classicShelbyCon:hover .image {
  opacity: 0.3;
}

.histButton {
  width: 100%;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  background-color: white;
  margin-top: 1.2rem;
  font-size: 1.2rem;
}

.histButton:hover {
  background-color: #9068be;
  box-shadow: 2px 5px;
}

.padPar {
  padding-top: 20px;
}

.justify {
  text-align: justify;
}

.paddingPars {
  padding: 10px;
}

@media (max-height: 991px) {
  #historyInfo {
    display: block;
  }
}

#form-div {
  width: 90%;
  max-width: 1000px;
  margin: 1rem auto;
  border: black thin solid;
  background-color: white;
  box-shadow: 5px 10px;
}

.form-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0.5rem 0;
}

.form-input {
  width: 100%;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  border: black thin solid;
}

.form-input:hover {
  background-color: #6ed3cf;
  box-shadow: 1px 3px;
}

#form-block input,
#form-block label {
  padding: 0.2rem 1rem;
  font-size: 1.2rem;
  color: inherit;
}

#form-submit {
  margin: 0 auto;
  width: 90%;
}

input[type="submit"]:hover {
  background-color: #7dce94;
  box-shadow: 2px 5px;
}

@media all and (min-width: 600px) {
  -webkit-flex-direction: row;
          flex-direction: row;
}

#staffContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  /* align-content: space-around; */
}

@media all and (max-width: 1199px) {
  #staffContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
  }
}

#cardContainer {
  box-shadow: 5px 10px #393f4d;
  max-height: 750px;
  max-width: 500px;
  min-width: 300px;
  margin: 3% auto;
  padding: 1rem;
  border: black medium solid;
  background-color: #f6f5f3;
  /* padding: 0;
  margin: 0; */
}

.spanCard {
  margin: 0;
}

#portrait {
  border: #393f4d medium solid;
  display: block;
  margin: 5px auto;
}

